<template>
  <div id="app">
    <Header :seller="seller"></Header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link tag="div"  active-class="active" :to="{name:'goods'}" >商品</router-link>
      </div>
      <div class="tab-item">
        <router-link tag="div"  active-class="active" :to="{name:'ratings'}">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link tag="div" active-class="active" :to="{name:'seller',params:{key:123}}">商家</router-link>
      </div>
    </div>
  <keep-alive>
    <router-view :seller="seller"></router-view>
   </keep-alive>
  </div>
</template>

<script>
import myajax from './api/myajax.js'
import {urlParse} from './common/js/util.js'
import Header from "./components/Header/Header"

export default {
  name: 'App',
  data(){
    return {
      seller:{
        id:(()=>{
          let queryParam=urlParse();
          console.log(queryParam)
          return queryParam.id
        })()
      }
    }
  },
  created(){
    myajax.axios({
      method:'get',
      // url:"../../static/data.json",
      url:"http://127.0.0.1:8080/static/data.json",
      responseType:"json",
      success:(res)=>{
        // util内解析路径为http://192.168.10.52:8080/?id=12345&a=b=/#/goods
        // this.seller=res.seller
      // console.log(this.seller)
      this.seller=Object.assign({},this.seller,res.seller)
      console.log(this.seller.id)
      }
    })
  },
  components:{
    Header
  }
}
myajax.axios({
        method:'get',
        // url:"../../static/data.json" ,
        url:"http://127.0.0.1:8080/static/data.json",
	      data:{type:3},
	      responseType:"json",
	      success: (res) => {
	        console.log(res)
//	        this.banners = res.data
	      }
    	})
</script>

<style lang="stylus" rel="stylus">
@import "./common/stylus/mixin.styl";
  #app
    .tab
      width:100%;
      height :40px;
      line-height :40px;
      display :flex;
      border-1px(rgba(7,17,27,0.1))
      .tab-item
        flex:1;
        text-align:center; 
        font-size 14px;
        color :rgb(77,85,83)
        .active
          color rgb(240,20,20)
</style>



