<!--  -->
<template>
  <transition name="move">
      <div v-show="showFlag" class="foods" ref="food">
          <div class="food-content">
              <div class="image-header">
                  <img :src="food.image" alt="">
                  <div class="back" @click="hide">
                      <i class="icon-arrow_lift"></i>
                  </div>
              </div>
              <div class="contents">
                  <div class="title">{{food.name}}</div>
                  <div class="detail">
                    <span class="sell-count">月售{{food.sellCount}}份数</span>
                    <span class="rating">好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class='now'>¥{{food.price}}</span>
                    <span class='old' v-show='food.oldPrice'>¥{{food.oldPrice}}</span>
                  </div>
                  <div class="carcontrol-wrapper">
					<cartcontrol v-on:cartAdd="cartAdd" :food='food'></cartcontrol> 
                  </div>
                    <transition name='fades'>
                        <div class="buy" @click.stop.prevent='addFirst' v-show='!food.count || food.count === 0'>加入购物车</div>
                    </transition>
              </div>
              <split  v-show="food.info"></split>
              <div class="info" v-show="food.info">
                  <h1 class="title">商品信息</h1>
                  <p class="text" >{{food.info}}</p>
              </div>
              <split  v-show="food.info"></split>
              <div class="rating">
                  <h1 class="title">商品评价</h1>
                  <ratingSelect :select-type="selectType" 
                  :onlyCOntent='onlyCOntent' :desc="desc" :ratings="food.ratings"
                   @select='selectRating'  @toggle="toggleContent"></ratingSelect>
                <div class="rating-wrapper">
                    <ul v-show="food.ratings && food.ratings.length">
                        <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
                            <div class="user">
                                <span class="name">{{rating.username}}</span>
                                <img class="avatar" :src="rating.avatar" width="12" height="12" alt="">
                            </div>
                            <div class="time">{{rating.rateTime | formatDate}}</div>
                            <p class="text">
                                <span :class="{'icon-thumb_up':rating.rateType==0,'icon-thumb_down':rating.rateType==1,}"></span>
                                <span class='text'>{{rating.text}}</span>
                            </p>
                        </li>
                    </ul>
                    <div class="no-rating" v-show="!food.ratings || food.ratings.length"></div>
                </div> 
              </div>
          </div>
    </div>
  </transition>
</template>

<script>
import {formatDate} from './../../common/js/date.js'
import cartcontrol from './../cartcontrol/cartcontrol.vue'
import split from './../split/split.vue'
import BScroll from 'better-scroll';
import Vue from 'vue'
import ratingSelect from './../ratingSelect/ratingSelect.vue'

const   POSITIVE=0;//正向
const   NEGATIVE=1;//反向
const   ALL=2;//所有
export default {
    props:{
        food:{
            type:Object
        }
    },
  data () {
    return {
        showFlag:false,
        selectType:ALL,
        onlyCOntent:true,
        desc:{
            all:"全部",
            positive:"推荐",
            negative:"吐槽"
        }
    };
  },

  components: {
      cartcontrol,
      split,
      ratingSelect
  },
    events:{
        select(type){
            this.selectType=type;
            this.$nextTick(()=>{
                this.scroll.refresh();
            })
        },
        toggle(onlyCOntent){
            this.onlyCOntent=onlyCOntent;
            this.$nextTick(()=>{
                this.scroll.refresh();
            })
        }
    },
  computed: {},


  methods: {
      show(){//本身组件事件不带下划线
          this.showFlag=true;
          this.selectType=ALL;
          this.onlyCOntent=false;
          this.$nextTick(()=>{
              if(!this.scroll){
                  this.scroll=new BScroll(this.$refs.food,{
                      click:true
                  })
              }
              else{
                  this.scroll.refresh();
                    // 或者
                    // this.showFlag=false
              }
          })
      },
      hide(){
          this.showFlag=false
      },
      addFirst(event){
          if(!event._constructed) {return;}
          this.$emit('cartAdd', event.target);
          Vue.set(this.food,"count",1)
      },
      cartAdd(el){
          this.$emit('cartAdd', el);
      },
      selectRating(type){
			this.selectType = type;
			this.$nextTick(() => {
				this.scroll.refresh();
			})
       },
       toggleContent(onlyCOntent){
           this.onlyCOntent=!onlyCOntent;
           this.$nextTick(() => {
				this.scroll.refresh();
			})
       },
       needShow(type,text){
           if(this.onlyCOntent && !text){
               return false
           }
           if(this.selectType==ALL){
               return true
           }else{
              return type==this.selectType
           }
       },
       
  },
  filters:{
		formatDate(time) {
			let date = new Date(time);
			return formatDate(date, 'yyyy-MM-dd hh:mm');
		}
    },
}

</script>
<style lang='stylus' rel='./stylus'>
@import "../../common/stylus/mixin"
    .foods
        position fixed;
        top 0;
        left 0;
        bottom 48px;
        z-index 30;
        width 100%;
        background #FFFFFF;
        &.move-enter-active,&.move-leave-active{
			transition: all 0.5s linear;
        }
		&.move-enter,&.move-leave-to{
			transform: translate3d(100%,0,0)
        }
        .image-header
            position relative;
            height 0;
            width 100%;
            padding-top 100%;
            img
                position absolute;
                width 100%;
                height 100%;
                top 0;
                left 0;
            .back
                position absolute;
                top 10px;
                left 0;
                .icon-arrow_lift
                    display block;
                    padding 10px;
                    color #00a0dc;
                    font-size 20px
        .contents
            position relative;
            padding 18px;
            .title
                color rgb(7,17,27)
                line-height 14px;
                margin-bottom 8px;
                font-size 14px;
                font-weight 700;
            .detail
                margin-bottom 18px;
                line-height 10px;
                height 10px;
                font-size 0;
                .sell-count,.rating
                    font-size 10px;
                    color rgb(147,153,159)
                .rating
                    margin-left 12px;
            .price
                font-weight 700;
                line-height 24px;
                .now
                    margin-right 8px
                    font-size 14px;
                    color rgb(240,20,20)
                .old
                    text-decoration line-through
                    font-size 10px;
                    color rgb(147,153,159);
        .carcontrol-wrapper
            position absolute;
            right 12px;
            bottom 12px;
        .buy
            position absolute
            right 18px;
            bottom 18px;
            z-index 10;
            line-height 24px;
            height 24px;
            padding 0 12px;
            box-sizing border-box;
            font-size 10px;
            border-radius 12px;
            color #ffffff;
            background rgb(0,160,220);
            opacity 1;
            &.fades-enter-active,&.fades-leave-active
                transition all .2s;
            &.fades-enter,&.fades-leave-to
                opacity 0;
        .info
            padding 18px;
            .title
                line-height 14px;
                margin-bottom 6px;
                font-size 14px;
                color rgb(7,17,27);
            .text
                line-height 24px;
                padding 0 8px;
                font-size 12px;
                color rgb(77,85,93)
                text-indent 28px; 
        .rating
            padding-top 18px; 
            .title
                line-height 14px;
                margin-left 18px;
                font-size 14px;
                color rgb(7,17,27);
            .rating-wrapper
                padding: 0 18px;
                position relative;
                .rating-item
                    padding 16px 0;
                    border-1px rgba(7,17,27,.1);
                    .user
                        position absolute;
                        right 0;
                        top 16px;
                        font-size 0;
                        line-height 12px;
                        .name
                            display inline-block;
                            vertical-align top;
                            font-size 10px;
                            margin-right 6px;
                            color rgb(147,153,159)
                        .avatar
                            border-radius 50%;
                    .time
                        font-size 10px;
                        line-height 12px;
                        color rgb(147,153,159);
                        margin-bottom 6px;
                    .text
                        line-height 16px;
                        font-size 12px;
                        font-weight 700;
                        color rgb(7,17,27)
                        .icon-thumb_up,.icon-thumb_down
                            margin-right 4px;
                            line-height 16px;
                            font-size 12px;
                        .icon-thumb_up
                            color rgb(0,160,220)
                        .icon-thumb_down
                            color rgb(147,153,159)

            

</style>


