import Vue from 'vue'
import Router from 'vue-router'
import goods from '@/components/goods/goods'
import seller from '@/components/seller/seller'
import ratings from '@/components/ratings/ratings'
Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
  		path:'/',
  		redirect:'/goods'
    },
    {
      path: '/goods',
      name:'goods',
      component: goods
    },
    {
      path: '/seller',
      params: { userId: 123 },
      name:'seller',
      
      component: seller
    },
    {
      path: '/ratings',
      name:'ratings',
      component: ratings
    }
  ]
})



