<template>
    <div class="header">
        <div class="content-wrapper">
            <div class="avatar">
                <img width="64" height="64" :src="seller.avatar">
            </div>
            <div class="content">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="description">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>
                <div v-if="seller.supports" class="supports">
                    <span class="icon" :class="classMap[seller.supports[2].type]"></span>
                    <span class="text">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div v-if="seller.supports" class="suppport-count" @click="showDetail">
                    <span class="count">{{seller.supports.length}}个</span>
                    <i class="icon-keyboard_arrow_right"></i>
            </div>
        </div>
        <div class="bulletin-wrapper" @click="showDetail">
            <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
            <i class="icon-keyboard_arrow_right"></i>
        </div>
        <div class="background">
            <img :src="seller.avatar" alt="" width="100%" height="100%">
        </div>
        <div v-show="detailShow" class="detail" transition='fade'>
            <div class="detail-warpper clearfix">
                <div class="detail-main">
                    <div class="detail-title">{{seller.name}}</div>
                    <div class="star-warpper">
                        <start :size='48' :score='seller.score'></start>
                    </div>
                    <div class="discountMsg">
                        <div class="lineSty"></div>
                        <div class="msgTitle">优惠信息</div>
                        <div class="lineSty"></div>
                    </div>
                   <ul v-if="seller.supports" class="supports">
                    <li class="supports-item" v-for="(item) in seller.supports">
                        <span class="icon" :class="classMap[item.type]"></span>
                        <span class="text">{{item.description}}</span>
                    </li>
                   </ul>
                   <div class="discountMsg">
                        <div class="lineSty"></div>
                        <div class="msgTitle">商家公告</div>
                        <div class="lineSty"></div>
                    </div>
                    <div class="bulletin">
                        <p class="content">{{seller.bulletin}}</p>
                    </div>
                </div>
            </div>
            <div class="detail-close" @click="hideDetail">
                <i class="icon-close"></i>
            </div>
        </div>
    </div>
</template>
<script>
// import myajax from '../../api/myajax.js'
import start from '../start/start';
export default {
    props:{
        seller:{
            type:Object
        }
    },
    components:{
        start
    },
    data(){
        return {
            detailShow:false
        }
    },
    methods:{
       showDetail(){
           this.detailShow=true;
       },
       hideDetail(){
           this.detailShow=false;
       }
    },
    created() {
        this.classMap=['decrease','discount','tejia','invoice','guarantee']
    }
}
</script>

<style lang="stylus" rel="stylus">
@import "../../common/stylus/mixin";
  .header
    color:#FFF;
    background:rgba(7,17,27,0.5);
    overflow hidden;
    position relative;
    .content-wrapper
        padding:24px 12px 18px 24px;
        font-size:0;
        position relative;
        .avatar
            margin-left:16px;
            display inline-block;
            img
                border-radius:2px;
        .content
            display inline-block;
            font-size:14px;
            margin-left:16px;
        .title
            margin :2px 0 8px 0;
            .brand
                vertical-align:top;
                display :inline-block;
                width:30px;
                height:18px;
                bg-image('brand');
                background-size:30px 18px;
                background-repeat :no-repeat;
            .name
                margin-left:6px;
                font-size:16px;
                line-height:18px;
                font-weight:bold;
        .description
            margin-bottom :10px;
            line-height :12px;
            font-size:12px;
        .supports
            font-size:12px;
            .icon
              display:inline-block;
              width:12px;
              height:12px;
              vertical-align:top;
              margin-right:4px;
              background-size:12px 12px;
              background-repeat:no-repeat;
              &.decrease
                bg-image('decrease_1')
              &.discount
                bg-image('discount_1')
              &.guarantee
                bg-image('guarantee_1')
              &.invoice
                bg-image('invoice_1')
              &.tejia
                bg-image('tejia1')
            .text
                line-height 12px
                font-size 10px;
        .suppport-count
            position absolute;
            right:12px;
            bottom 18px;
            padding 0 8px;
            height 24px;
            line-height 24px;
            border-radius:14px;
            background-color rgba(0,0,0,.2);
            text-align center;
            .count
                font-size 10px;
                vertical-align top;
            .icon-keyboard_arrow_right
                font-size 10px; 
                line-height 24px;
    .bulletin-wrapper
        height 28px;
        line-height 28px;
        padding 0 22px 0 12px;
        white-space nowrap;
        overflow hidden;
        text-overflow ellipsis;
        position relative;
        background-color rgba(7,17,27,.2)
        .bulletin-title
            display inline-block;
            vertical-align top;
            width 22px;
            height 12px;
            bg-image('bulletin');
            background-size 22px 12px;
            background-repeat:no-repeat;
            margin-top 8px;
        .bulletin-text
            vertical-align top;
            font-size:10px;
            margin 0 4px;
        .icon-keyboard_arrow_right
            position absolute;
            fon-size 10px;
            right 12px;
            top 6px;
    .background
        position absolute;
        top 0;
        left 0;
        width 100%;
        height 100%;
        z-index -1;
        filter blur(10px)
    .detail
        position fixed;
        z-index 100;
        width 100%;
        height 100%
        overflow hidden;
        top 0
        left 0
        overflow auto;
        transition all 0.5s;
        &.fade-transition
            opacity 1
            background rgba(7,17,27,0.8);
        &.fade-enter,&.fade-leave
            opacity 0
            background rgba(7,17,27,0);
            blur 10px;
        .detail-warpper
            min-height:100%;
            width: 100%;
            background rgba(7,17,28,.8)
            .detail-main
                margin-top 64px;
                padding-bottom 64px;
                .detail-title
                    width 100%;
                    text-align center;
                    font-size:16px;
                    line-height 16px;
                    font-weight:700;
                    color:rgb(255,255,255);
                .star-warpper
                    margin-top 18px;
                    padding 2px 0;
                    text-align center
                .discountMsg
                    display flex;
                    flex-direction row;
                    justify-content center;
                    align-items center;
                    margin 28px auto 24px auto;
                    .lineSty
                        width 33%;
                        height 1px;
                        background-color rgba(255,255,255,.2)
                    .msgTitle
                        font-size 14px;
                        font-weight 700;
                        line-height 14px;
                        color rgb(255,255,255);
                        margin 0 12px;
                .supports
                    width 80%;
                    margin 0 auto 
                    .supports-item
                        padding 0 12px;
                        margin-bottom 12px;
                        font-size 0;
                        &:last-child
                            margin-bottom 0;
                        .icon
                            display inline-block;
                            width 16px;
                            height 16px;
                            vertical-align top;
                            margin-right 6px;
                            background-size 16px;
                            background-repeat no-repeat;
                            &.decrease
                                bg-image('decrease_2')
                            &.discount
                                bg-image('discount_2')
                            &.guarantee
                                bg-image('guarantee_2')
                            &.tejia
                                bg-image('tejia2')
                            &.invoice
                               bg-image('invoice_2')
                        .text
                            line-height 16px;
                            font-size 12px;
                .bulletin
                    width 80%;
                    margin 0 auto;
                    padding 0 12px;
                    p
                        line-height 24px;
                        font-weight:200;
                        color rgb(255,255,255)
                        font-size 12px;

        .detail-close
            position relative
            width 32px;
            height 32px;
            margin:-64px auto 0 auto;
            clear:both;
            font-size:32px;   
            color red;
                

</style>





