<!--  -->
<template>
  <div class="goods">
    <div class="menu-wrapper" ref='menuWrapper'>
        <ul class="menu-ul">
          <li v-for="(item,index) in goods" class="menu-item menu-list-hook"  :class="{'current':currentIndex===index}" @click="selectMenu(index,$event)">
            <!-- -->
            <span class="text border-1px">
              <span class="icon" v-show="item.type>0" :class="classMap[item.type]"></span>{{item.name}}
            </span>
          </li>
        </ul>
    </div>
    <div class="foods-wrapper" ref='foodsWrapper'>
        <ul>
          <li v-for="item in goods" class="foods-list food-list-hook">
            <h1 class="title">{{item.name}}</h1>
            <ul>
              <li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
                <div class="icon">
                  <img :src="food.icon" alt="">
                </div>
                <div class="content">
                  <h2 class="name">{{food.name}}</h2>
                  <p class="desc">{{food.description}}</p>
                  <div class="extra">
                    <span>月售{{food.sellCount}}</span><span>好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food" v-on:cartAdd="cartAdd"></cartcontrol>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
    </div>
    <shopcart ref="shopcart" :select-foods='selectFoods' :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    <food :food="selectedFood" @cartAdd='cartAdd' ref="foods"></food>
  </div>
</template>

<script>
import myajax from './../../api/myajax.js';
import BScroll from 'better-scroll';
import shopcart from './../shopcart/shopcart.vue'
import cartcontrol from './../cartcontrol/cartcontrol.vue'
import food from './../food/food.vue'
export default {
  props:{
    seller:{
      type:Object
      }
  },
  data (){
    return {
      goods:[],
      listHeight:[],
      scrollY:0,
      currentY:0,
      selectedFood:{}
    }
  },
  created(){
    this.classMap=['decrease','discount','special','invoice','guarantee']
    myajax.axios({
      method:"get",
      // url:"../../static/data.json",
      url:"http://127.0.0.1:8080/static/data.json",
      responsetype:"json",
      success:(res)=>{
        this.goods=res.goods;
        this.$nextTick(()=>{//dom更新完毕
           this._initScroll();
           this._calculateHeight();
        })
      }
    })
  },
  computed:{
    currentIndex(){
      for(let i=0;i<this.listHeight.length;i++){
        let height1=this.listHeight[i];
        let height2=this.listHeight[i+1];
        if(!height2||(this.scrollY>=height1&&this.scrollY<height2)){
          return i
        }
      }
      return 0;
    },
    selectFoods(){
      let foods=[];
      this.goods.forEach((good)=>{
        good.foods.forEach((food)=>{
          if(food.count){
            foods.push(food)
          }
        })
      })
      return foods
    },
  },
  methods:{
      selectMenu(index,event){
        if(!event._constructed){return;}
        let foodList=this.$refs.foodsWrapper.getElementsByClassName("food-list-hook");
        let el=foodList[index];
        let menuList=this.$refs.menuWrapper.getElementsByClassName("menu-list-hook");
        let mel=menuList[index];
        let viewHeight=this.$refs.menuWrapper.clientHeight;//可视区域高度
        let offTop=mel.offsetTop;//当前元素距父元素高度
        // console.log(offTop,this.$refs.menuWrapper.clientHeight);
        let abs=Math.abs(offTop-viewHeight);//绝对值
        if(offTop<80){
          this.menuScroll.scrollTo(0,0,700)
        }else{
          if(abs<108){
            this.menuScroll.scrollToElement(mel,700)
          }
        }
        this.foodsScroll.scrollToElement(el,300)
      },
     cartAdd(target){
        this.$refs.shopcart.drop(target)
     },
    _initScroll(){
      this.menuScroll=new BScroll(this.$refs.menuWrapper,{//找到dom挂载点
          click:true,
          freeScroll:true,
          probeType:3
      });
      this.menuScroll.on("scroll",(pos)=>{
        var _this=this;
        this.scrollMenuY=Math.abs(Math.round(pos.y));
      })
      this.foodsScroll=new BScroll(this.$refs.foodsWrapper,{
         click:true,
         probeType:3,//作用：有时候我们需要知道滚动的位置。当 probeType 为 1 的时候，会非实时（屏幕滑动超过一定时间后）派发scroll 事件；
        //当 probeType 为 2 的时候，会在屏幕滑动的过程中实时的派发 scroll 事件；当 probeType 为 3 的时候，不仅在屏幕滑动的过程中，
        //而且在 momentum 滚动动画运行过程中实时派发 scroll 事件。如果没有设置该值，其默认值为 0，即不派发 scroll 事件。
      });
      this.foodsScroll.on("scroll",(pos)=>{
        var _this=this;
        this.scrollY=Math.abs(Math.round(pos.y));
      })
    },
    _calculateHeight(){
      let foodList=this.$refs.foodsWrapper.getElementsByClassName("food-list-hook");
      let height=0;
      this.listHeight.push(height);
      for(let i=0;i<foodList.length;i++){
        let item =foodList[i];
        height+=item.clientHeight;
        this.listHeight.push(height)
      }
    },
    selectFood(food,event){
      console.log(food)
      if(!event._constructed){return;}
      this.selectedFood=food;
      this.$refs.foods.show()
    },
  },
  components:{
    shopcart,
    cartcontrol,
    food
  },
}

</script>
<style lang='stylus'>
@import "../../common/stylus/mixin";
  .goods
    display flex;
    position absolute;
    width 100%;
    top 174px;
    bottom 46px;
    display flex;
    overflow hidden;
    .menu-wrapper
      flex 0 0 80px;
      width 80px;
      background #f3f5f7;
      .menu-item
        display table;
        line-height 14px;
        height 54px;
        width 56px;
        padding 0 12px;
        &.current
          position relative;
          margin-top -1px;
          z-index 10
          background #FFF;
          font-weight 700;
          .text
            border-none()
        .icon 
              display:inline-block;
              width:12px;
              height:12px;
              vertical-align:top;
              margin-right:2px;
              background-size:12px 12px;
              background-repeat:no-repeat;
              &.decrease
                bg-image('decrease_3')
              &.discount
                bg-image('discount_3')
              &.guarantee
                bg-image('guarantee_3')
              &.invoice
                bg-image('invoice_3')
              &.special
                bg-image('special_3')  

        .text
          display table-cell;
          vertical-align middle;
          border-1px(rgba(7,17,27,0.1))
          font-size 12px;

    .foods-wrapper
      flex 1
      .title
        padding-left 14px;
        height 26px;
        line-height 26px;
        border-left 2px solid #dedde1;
        font-size 12px;
        color rgb(147,153,159)
        background #f3f5f7;
      .food-item
        display flex;
        margin 18px;
        padding-bottom 18px;
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none();
          margin-bottom 0;
        .icon
          flex 0 0 57px;
          margin-right 10px;
        .content
          flex 1;
          .name
            margin 2px 0 8px 0;
            overflow hidden
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            color rgb(2,17,27)
            font-size 14px;
            line-height 14px;
            height 14px;
          .desc
            line-height 13px;
            margin-bottom 8px;
            font-size 10px;
            color rgb(147,153,159)
          .extra
            line-height 10px;
            font-size 10px;
            color rgb(147,153,159);
            span:last-child
              margin-left 12px;
            @media screen and (max-width 320px){
              line-height 16px;
            }
          .price
            font-weight 700;
            line-height 24px;
            .now
              margin-right 8px
              font-size 14px;
              color rgb(240,20,20)
            .old
              text-decoration line-through
              font-size 10px;
              color rgb(147,153,159);
          .cartcontrol-wrapper
            position absolute;
            right 0;
            bottom 12px;
</style>


