<!--  -->
<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
            <div class="score-wrapper">
              <span class="title">服务态度</span>
              <start class="start" :size="36" :score="seller.serviceScore"></start>
              <span class="score">{{seller.serviceScore}}</span>
            </div>
            <div class="score-wrapper">
              <span class="title">商品评分</span>
              <start class="start" :size="36" :score="seller.foodScore"></start>
              <span class="score">{{seller.foodScore}}</span>
            </div>
            <div class="delivery-wrapper">
              <span class="title">送达时间</span>
              <span class="delivery">{{seller.deliveryTime}}分钟</span>
            </div>
        </div>
      </div> 
      <split></split>
      <ratingSelect :select-type="selectType" :onlyCOntent='onlyCOntent' :desc="desc" :ratings="ratings"
        @select='selectRating'  @toggle="toggleContent"></ratingSelect>
        <div class="rating-wrapper">
          <ul>
            <li v-for="rating in ratings" class="rating-item"v-show="needShow(rating.rateType,rating.text)">
                <div class="avatar">
                  <img :src="rating.avatar" alt="" width="28" height="28">
                </div>
                <div class="content">
                  <h1 class="name">{{rating.username}}</h1>
                  <div class="star-wrapper">
                    <start :size="24" :score="rating.score"></start>
                    <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
                  </div>
                  <p class="text">{{rating.text}}</p>
                  <div class="recommend" v-show="rating.recommend&&rating.recommend.length">
                    <span class="icon-thumb_up"></span>
                    <span v-for="item in rating.recommend" class="item">{{item}}</span>
                    <div class="time">{{rating.rateTime|formatDate}}</div>
                  </div>
                  
                </div>
              
            </li>
          </ul>
        </div>
    </div>
  </div>
</template>
<script>
import {formatDate} from './../../common/js/date.js'
import start from './../start/start.vue';
import split from './../split/split.vue'
import myajax from './../../api/myajax.js';
import BScroll from 'better-scroll';
import ratingSelect from './../ratingSelect/ratingSelect.vue'
const   POSITIVE=0;//正向
const   NEGATIVE=1;//反向
const   ALL=2;//所有
export default {
  props:{
    seller:{
      type:Object
    }
  },
  data(){
    return {
        ratings:[],
        selectType:ALL,
        onlyCOntent:true,
        desc:{
            all:"全部",
            positive:"满意",
            negative:"不满意"
        }
    }
  }, 
  components:{
    start,
    split,
    ratingSelect
  },
  computed:{

  },
   filters:{
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
  },
  events:{
        select(type){
            this.selectType=type;
            this.$nextTick(()=>{
                this.scroll.refresh();
            })
        },
        toggle(onlyCOntent){
            this.onlyCOntent=onlyCOntent;
            this.$nextTick(()=>{
                this.scroll.refresh();
            })
        }
  },
  created(){
    myajax.axios({
      method:"get",
      // url:"../../static/data.json",
      url:"http://127.0.0.1:8080/static/data.json",
      responsetype:"json",
      success:(res)=>{
        this.ratings=res.ratings;
      }
    })
    this.$nextTick(()=>{
      if(!this.scroll){
        this.scroll=new BScroll(this.$refs.ratings,{
          click:true
        })
      }
    })
  },
  methods:{
    selectRating(type){
			this.selectType = type;
			this.$nextTick(() => {
				this.scroll.refresh();
			})
    },
    toggleContent(onlyCOntent){
        this.onlyCOntent=!onlyCOntent;
        this.$nextTick(() => {
				this.scroll.refresh();
			})
    },
    needShow(type,text){
           if(this.onlyCOntent && !text){
               return false
           }
           if(this.selectType==ALL){
               return true
           }else{
              return type==this.selectType
           }
    },
  }
}

</script>
<style lang='stylus' rel='./stylus'>
    @import "../../common/stylus/mixin"
      .ratings
        position absolute;
        top 174px;
        bottom 0;
        left 0;
        width 100%;
        overflow hidden;
        .overview
          display flex;
          padding 18px 0;
          .overview-left
            padding 6px 0;
            flex 0 0 137px;
            width 137px;
            border-right 1px solid rgba(2,17,27,.1);
            text-align center;
            @media only screen and (max-width 320px){
              flex 0 0 120px;
              width 120px;
            }
            .score
              margin-bottom 6px;
              line-height 28px;
              font-size 24px;
              color rgb(255,153,0);
            .title
              line-height 24px;
              font-size 12px;
              margin-bottom 8px;
              color rgb(7,17,27);
            .rank
              color rgb(147,153,159);
              line-height 10px;
              font-size 10px;
          .overview-right
            flex 1;
            padding 6px 0 6px 24px;
            @media only screen and (max-width 320px){
              padding-left 6px
            }
            .score-wrapper
              margin-bottom 8px;
              font-size 0;
              .title
                line-height 18px;
                font-size 12px;
                color rgb(14,23,32)
                display inline-block;
                vertical-align top;
              .start
                display inline-block;
                vertical-align top;
                margin 0 12px;
              .score
                display inline-block;
                line-height 18px;
                vertical-align top;
                font-size 12px;
                color rgb(255,153,0)
            .delivery-wrapper
                font-size 0;
                .title
                  line-height 18px;
                  font-size 12px;
                  color rgb(14,23,32);
                .delivery
                  margin-left 12px;
                  font-size 12px;
                  color rgb(147,153,159)
        .rating-wrapper
          padding 0 18px
          .rating-item
            display flex;
            padding 18px 0;
            border-1px (rgba(7,12,27,.1))
            .avatar
              flex 0 0 28px;
              width 28px;
              margin-right 12px;
              img 
                border-radius 50%;
            .content
              flex 1;
              position relative;
              .name
                margin-bottom 10px;
                line-height 12px;
                font-size 10px;
                color rgb(7,17,27);
              .star-wrapper
                font-size 0;
                margin-bottom 6px;
                .star
                  display inline-block;
                  margin-right 6px;
                  vertical-align top;
                .delivery
                  display inline-block;
                  vertical-align top;
                  margin-left 12px;
                  line-height 12px;
                  font-size 12px;
                  color rgb(147,159,153);
              .text
                line-height 18px;
                color rgb(7,17,27);
                font-size 12px;
                margin-bottom 8px;
              .recommend
                line-height 16px;
                font-size 0;
                .icon-thumb_up,.item
                    display inline-block;
                    margin 0 8px 4px 0;
                    font-size 9px;
                .icon-thumb_up
                  color rgb(0,160,220);
                .item
                  padding 0 6px;
                  border 1px solid rgba(7,17,27,.1);
                  border-radius 1px;
                  background #ffffff
                .time
                  position absolute;
                  top 0;
                  right 0;
                  line-height 12px;
                  font-size 10px;
                  color rgb(147,159,153);

</style>


