<!--  -->
<template>
  <div class="cartcontrol">
      <transition name="move">
          <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="cartDecrease">
            <span class="inner icon-remove_circle_outline"></span>
          </div>
      </transition>
      
      <div class="cart-count"  v-show="food.count>0">{{food.count}}</div>
      <div class="cart-add icon-add_circle" @click.stop.prevent="addCart"></div>
  </div>
</template>

<script>
import Vue from "vue";
export default {
    props:{
        food:{type:Object}
    },
  data () {
    return {
    };
  },
  created() {
    //   console.log(this.food)
    },
  components: {},

  computed: {},


  methods: {
      addCart(event){
          if (!event._constructed) {
          return;
        }
          if(!this.food.count){
              Vue.set(this.food,"count",1)
            // this.food.count=1
          }else{
              this.food.count++;
          }
          this.$emit("cartAdd",event.target)
      },
      cartDecrease(){
          if(this.food.count==0){//避免减为负值
            return
          }
          this.food.count--;
      }
  }
}

</script>
<style lang='stylus' rel='./stylus'>
    .cartcontrol
        font-size 0;
        .cart-decrease
            display inline-block
            padding 6px;
            transition all 0.4s linear
            &.move-transition
              opacity 1;
              transform translate3d(0,0,0);
            .inner
                display inline-block;
                transition all 0.4s linear;
                transform rotate(0)
                line-height 24px;
                font-size 24px;
                color rgb(0,160,220)
            &.move-enter,&.move-leave-active
                opacity 0;
                transform translate3d(24px,0,0)
                .inner
                    transition all 0.4s linear;
                    transform rotate(180deg)
        .cart-count
            display inline-block
            font-size 10px;
            vertical-align top;
            width 12px;
            padding-top 6px;
            line-height 24px;
            text-align center;
            color rgb(147,153,159)
        .cart-add
            display inline-block
            padding 6px;
            line-height 24px;
            font-size 24px;
            color rgb(0,160,220)
            
</style>


