<!--  -->
<template>
  <div class="shopcart">
      <div class="content" @click="toggleList">
          <div class="content-left">
              <div class="logo-wrapper">
                  <div class="logo" :class="{'heghlight':totalCount>0}">
                      <i class="icon-shopping_cart"  :class="{'heghlight':totalCount>0}"></i>
                  </div>
                  <div class="num" v-show="totalCount>0">{{totalCount}}</div>
              </div>
              <div class="price" :class="{'heghlight':totalPrice>0}">￥{{totalPrice}}</div>
              <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
          </div>
          <div class="content-right">
              <div class="pay" v-if="totalPrice==0">
                  ￥{{minPrice}}元起送
              </div>
              <div class="pay bgcolor" v-else-if="totalPrice>=minPrice" @click.stop="pay">
                  去结算
              </div>
              <div class="pay" v-else>
                  还差{{minPrice-totalPrice}}元起送
              </div>
          </div>
          <div class="ball-container">
              <div v-for="ball in balls">
					<transition name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
						<div class="ball" v-show="ball.show">
							<div class="inner inner-hook"></div>
						</div>
					</transition>
				</div>
          </div>
        <transition name="fold">
            <div class="shopcar-list" v-show="listShow">
              <div class="list-heafer">
                  <h1 class="title">购物车</h1>
                  <span class="empety" @click.stop="empety">清空</span>
              </div>
              <div class="list-content" ref="listcon">
                  <ul>
                      <li class="food" v-for="food in selectFoods">
                          <span class="name">{{food.name}}</span>
                          <div class="price">
                              <span>￥{{food.price*food.count}}</span>
                          </div>
                          <div class="cartcontrol-wrapper">
                              <cartcontrol v-on:cartAdd="cartAdd" :food="food"></cartcontrol>   
                          </div>
                      </li>
                  </ul>
              </div>
            </div>
         </transition>
        <transition name='fade'>
			<div class="list-mask" v-show='listShow' @click='hideList'></div>
		</transition>
      </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll';
import cartcontrol from './../cartcontrol/cartcontrol.vue'
import { MessageBox, Toast } from 'mint-ui';
export default {
    props:{
        selectFoods:{
        type:Array,
        default(){
            return [{price:20,count:2}];
        }
    },
        deliveryPrice:{type:Number,default:0},
        minPrice:{type:Number,default:0}
    },
  data () {
    return {
        balls:[
            {show:false},
            {show:false},
            {show:false},
            {show:false},
            {show:false},
        ],
        dropBalls:[],
        fold:true
    };
  },

  components: {
      cartcontrol
  },

  computed: {
      totalPrice(){ 
          let total=0;
          this.selectFoods.forEach((food) => {
              total +=food.price*food.count
          });
          return total;
      },
      totalCount(){
            let count=0;
            this.selectFoods.forEach((food)=>{
                count+=food.count;
            })
            return count;
        },
      listShow(){
            if(!this.totalCount){
					this.fold = true;
					return false;
				}
                let show =(!this.fold);
				if(show){
					this.$nextTick(() => {
						if(!this.scroll){
							this.scroll = new BScroll(this.$refs.listcon,{
								click:true
                            })
                            // console.log(this.scroll)
						}
						else{
							this.scroll.refresh();
						}
					})
				}
				return show;
        },
  },
  methods: {
      drop(el){
        for(let i=0;i<this.balls.length;i++){
            let ball=this.balls[i];
            if(!ball.show){
                ball.show=true;
                ball.el=el;
                this.dropBalls.push(ball)
                return;
            }
        }
      },
        cartAdd(target){
            this.drop(target);
		},
        beforeEnter(el){
                 let count=this.balls.length;
                 while(count--){
                     let ball = this.balls[count];
                if(ball.show){
                     let rect = ball.el.getBoundingClientRect();
						let x = rect.left - 32;
						let y = -(window.innerHeight - rect.top - 22);
						el.style.display = '';
						el.style.webkitTransform = `translate3d(0,${y}px,0)`;
						el.style.transform = `translate3d(0,${y}px,0)`;
						let inner = el.getElementsByClassName('inner-hook')[0];
						inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
						inner.style.transform = `translate3d(${x}px,0,0)`;
                     }
                 }
        },
         enter(el,done){
                //   console.log(el)
                let rf=el.offsetHeight;//浏览器重置
                this.$nextTick(()=>{//体验异步优化
                     el.style.webkitTransform='translate3d(0,0,0)';
                     el.style.transform='translate3d(0,0,0)';
                     let inner=el.getElementsByClassName("inner-hook")[0];
                     inner.style.webkitTransform='translate3d(0,0,0)';
                     inner.style.transform='translate3d(0,0,0)';
                     el.addEventListener('transitionend', done);
                     //Vue 为了知道过渡的完成，必须设置相应的事件监听器。它可以是 transitionend 或 animationend 
                })
        },
        afterEnter(el){
                  let ball=this.dropBalls.shift();
                //   console.log(ball)
                  if(ball){
                      ball.show=false;
                      el.style.display="none";
                  }
        },
        toggleList(){
            if(!this.totalCount){//购物车没有商品的时候不可点击 
                return
            }
            this.fold=!this.fold//当前是收起状态就展开，展开状态就收起
        },
        empety(){
            this.selectFoods.forEach((food)=>{
                food.count=0;
            })
        },
        hideList(){
            return false;
        },
        pay(e){
            MessageBox({
            title: '提示',
            message: '确定提交订单吗?',
            showCancelButton: true
            }).then(action => {
                Toast(action);
            });
        },

  }
}

</script>
<style lang='stylus' rel='./stylus'>
    @import "../../common/stylus/mixin"
    .mint-msgbox-message,.mint-msgbox-confirm
        color #00A0DC;
    .mint-msgbox-cancel
        border-right 1.5px solid #ddd;
    

    .shopcart
        height 48px;
        width 100%;
        position fixed;
        left 0;
        bottom 0;
        z-index 50
        background #FFF;
        .content
            display flex ;
            background #141d27;
            font-size 0;
            color rgba(255,255,255,.4);
            .content-left
                flex 1;
                .logo-wrapper
                    display inline-block;
                    position relative
                    top -10px;
                    margin 0 12px;
                    padding 6px;
                    width 56px;
                    height 56px;
                    box-sizing border-box;
                    vertical-align top
                    border-radius 50%;
                    background #141d27;
                    .logo
                        width 100%;
                        height 100%;
                        border-radius 50%;
                        background rgba(43,52,60,0.8)
                        &.heghlight
                            background rgb(0,160,220)
                        text-align center;
                        .icon-shopping_cart
                           font-size 24px;
                           color #80858A;
                           line-height 44px;
                           &.heghlight
                               color #FFF
                    .num
                        position absolute;
                        top 0;
                        right 0;
                        width 20px;
                        height 16px;
                        line-height 16px;
                        text-align center;
                        border-radius 50%;
                        font-size 9px;
                        font-weight 400;
                        color #FFF;
                        background rgb(240,20,20);
                        box-shadow 0 4px 8px 0 rgba(0,0,0,0.4)
                .price
                    display inline-block;
                    font-size 24px;
                    line-height 24px;
                    margin-top 12px;
                    vertical-align top;
                    padding-right 12px;
                    box-sizing border-box;
                    border-right 1px solid rgba(255,255,255,.1)
                    font-size 16px;
                    font-weight 700;
                    &.heghlight
                        color #FFF
                .desc   
                    display inline-block;
                    vertical-align top;
                    line-height 24px;
                    margin 12px 0 0 12px;
                    font-size 10px;
            .content-right
                flex 0 0 105px;
                width 105px;
                .pay
                    height 48px;
                    line-height 48px;
                    font-size 12px;
                    text-align center;
                    font-weight 700;
                    background #2B333B;
                .bgcolor
                    background #0F9E42;
                    color #ffffff;
        .ball-container
            .ball
                position fixed;
                left 32px;
                bottom 22px
                z-index 200;
                transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
                .inner
                    width 16px;
                    height 16px;
                    border-radius:50%;
                    background rgb(0,160,220)
                    transition all 0.4s linear
        .shopcar-list
            position absolute
            left 0;
            top 0;
            z-index -1;
            width 100%;
            transform translate3d(0,-100%,0);
            &.fold-enter-active,&.fold-leave-active
                transition all 0.5s;
            &.fold-enter,&.fold-leave-to
                transform translate3d(0,0,0)
            .list-heafer
                height 40px;
                background #f3f5f7;
                line-height 40px;
                padding 0 18px;
                border-bottom 1px solid rgba(7,17,27,0.1)
                .title
                    float left
                    font-size 14px;
                    color rgb(7,17,27)
                .empety
                    float right 
                    font-size 12px;
                    color rgb(0,160,220)
            .list-content
                padding 0 18px;
                max-height 217px;
                overflow hidden;
                background #ffffff;
                .food
                    position relative;
                    padding 12px 0;
                    box-sizing border-box;
                    border-1px(rgba(7,17,27,0.1))
                    .name
                        line-height 24px;
                        font-size 14px;
                        color rgb(7,17,27)
                    .price
                        position:absolute;
                        bottom: 12px;
                        right: 90px;
                        line-height: 24px;
                        font-size :14px;
                        font-weight :700;
                        color: rgb(240,20,20);
                    .cartcontrol-wrapper
                        position absolute;
                        bottom 6px;
                        right 0;
        .list-mask
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -2;
            opacity: 1;
            background: rgba(7,17,27,0.6);
            &.fade-enter-active,&.fade-leave-active{
                transition: all .5s
            }
            &.fade-enter,&.fade-leave-to{
                opacity: 0;
                background: rgba(7,17,27,0);
            }
            

</style>


