import axios from "axios"
// const  baseUrl = "";
export default {
  axios(config){
    var { method, url, responseType, success, data } = config;
    axios({
      method: method || 'post',
      url:  url,
      data,
      responseType
    }).then((response) => {
      success(response.data);
    })
  }
}



